import { Component, OnInit, ViewChild, TemplateRef, HostListener, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-property-units-list',
  templateUrl: './property-units-list.component.html',
  styleUrls: ['./property-units-list.component.scss']
})
export class PropertyUnitsListComponent implements OnInit {

  isDataLoaded:boolean = false;

  blockListData:any;

  filterSelected:string = "all";
  singleBlock:string = "Select Tower";
  selectedBlockId:number;

  apartmentBlockId:any;
  apartmentBlockUnitId:any;

  unitDataList:any;
  columnData:any;
  gridSourceData:any;
  totalItems:any;
  unitData:any;  

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private router: Router,
    public dialog: MatDialog,
  	private apartmentService:ApartmentService,
    private sessionService:SessionService
  ) { }


  onGlSearchFilter() {

    if(this.unitData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.unitData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  getAllBlockData(){

  	this.filterSelected = 'all';
    this.singleBlock = "Select Block";
    
    var params = {
      apartmentId: this.sessionService.apartmentId
    }

  	this.apartmentService.getApartmentBlockUnitByApartmentId(params).subscribe((res:any) => {
     
      var unitDataList = res;

      this.gridSourceData.localdata = unitDataList;
      this.datagrid.updatebounddata('cells');

      this.totalItems = unitDataList.length;

  	})

  }

  getSingleBlock(block){
  	
  	this.filterSelected = 'single';
  	this.singleBlock = block.apartmentBlockNumber;
  	this.selectedBlockId = block.apartmentBlockId;

    var params = {
      apartmentId: this.sessionService.apartmentId
    }


  	this.apartmentService.getApartmentBlockUnitByApartmentId(params).subscribe((res:any) => {

      var unitDataList = res.filter(item => {
        return item.apartmentBlockNumber == this.singleBlock;
      })

      this.gridSourceData.localdata = unitDataList;
      this.datagrid.updatebounddata('cells');

      this.totalItems = unitDataList.length;

  	});

  }

  @HostListener('window:onEditUnitEvent', ['$event.detail'])
  onEditUnitEvent(detail) {

    

  }

  renderColumns() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
        text: 'Unit No',
        datafield: 'apartmentBlockUnitNumber',
        width: 180,
        pinned: true,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Block No',
        datafield: 'apartmentBlockNumber',
        width: 180,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Bulit Area SQ.M',
        datafield: 'builtupArea',
        width: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Unit Construction Area SQ.M',
        datafield: 'carpetArea',
        width: 240,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Unit Type',
        datafield: 'unitType',
        width: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Is Furnished',
        datafield: 'isFurnished',
        width: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Is PetsAllowed',
        datafield: 'isPetsAllowed',
        width: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Is Vacant',
        datafield: 'isVacant',
        width: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Actions',
        cellsalign: 'center', 
        align: 'center',
        minwidth: 120,
        datafield: 'isReversed',
        cellsrenderer: (row,coloumn,value): any => {

        let elemId = 'invoiceReverse'+ row;

          return '<div class="simple-actions">'
                    +'<a href="javascript:void(0)" class="mr-2 text-secondary" onClick="editUnitEvent('+ row +')">'
                      +'<i class="fa fa-pencil" aria-hidden="true"></i>'
                    +'</a>'
                    +'<a href="javascript:void(0)" class="mr-2 text-secondary" onClick="deleteUnitEvent('+ row +')">'
                      +'<i class="fa fa-trash" aria-hidden="true"></i>'
                    +'</a>'
                 +'</div>'
      },
      renderer: columnrenderer
      }
    ]


  }

  ngOnInit() {

    
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    
    this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
      
      this.blockListData = res;

      this.apartmentService.getApartmentBlockUnitByApartmentId(params).subscribe((res:any) => {

        console.log(res)

        this.filterSelected = 'all';
        this.singleBlock = "Select Block";

        var unitDataList = res;
        this.isDataLoaded = true;

        this.totalItems = unitDataList.length;

        this.gridSourceData = {
          localdata: unitDataList,
          datatype: "array"
        }

        this.unitDataList = new jqx.dataAdapter(this.gridSourceData);
        this.renderColumns();

  
      });

    });

  }

}

let editUnitEvent = row => {
  var event = new CustomEvent('onEditUnitEvent', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editUnitEvent = editUnitEvent;