import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PropertyAllUnitsComponent } from './property-all-units.component';
import { PropertyUnitsListComponent } from './property-units-list/property-units-list.component';

const routes: Routes = [
  {
		path     : '',
    component: PropertyAllUnitsComponent,
    children : [
			{ path: '', redirectTo: 'all-units', pathMatch : 'full' },
			{ path: 'all-units', 
        component: PropertyUnitsListComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PropertyAllUnitsRoutingModule { }
